import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/Login/Login.vue'
import Home from './views/Home/Home.vue'
import Chat from './views/Chat/Chat.vue'
import UserInfo from './views/UserInfo/UserInfo.vue'


Vue.use(Router)

export default new Router({
  linkActiveClass:'active',
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/Home',
      name: 'Home',
      component: Home,
      children:[
        {
         path: 'Info',
         name: 'Info',
         component: () => import(/* webpackChunkName: "Info" */ './views/Home/Info/Info.vue')
        },
        {
         path: 'File',
         name: 'File',
         component: () => import(/* webpackChunkName: "File" */ './views/Home/File/File.vue')
        },
        {
         path: 'Msg',
         name: 'Msg',
         component: () => import(/* webpackChunkName: "Msg" */ './views/Home/Msg/Msg.vue')
        },
      ]
    },
    {
      path: '/Chat',
      name: 'Chat',
      component: Chat
    },
    {
      path: '/UserInfo',
      name: 'UserInfo',
      component: UserInfo
    },
  ]
})
