<template>
	<div class="login" >
		<section v-if="!isReg">
			<div class="logo">QIM</div>
			<input id="text" type="text" v-model='name' placeholder="QQ号/手机号/邮箱">
			<input id="password" type="password" v-model='password' placeholder="密码">
			<button id="logBtn" type="button" @click="login()">登&nbsp录</button>
			<div class="login_left" @click="reg()">新用户注册</div>
			<div class="login_left login_right">无法登录</div>
		</section> 

		 <section class="sectionTwo" v-else>
			<input id="reg_text" type="text" v-model='name' placeholder="请输入账号">
			<input id="reg_password" type="password" v-model='password' placeholder="请输入密码">
			<div>
				<button	type="button" @click="adduser()">确&nbsp定</button>
				<button	type="button" @click="cancel()">取&nbsp消</button>
			</div>
		</section> 
	</div>
		
		
</template>
  
<script>
	import { theInput } from '../../assets/js/login.js'
	export default{
		name: 'Login',
		data(){
			return {
				isReg: false,
				name:'',
				password:'',
			}
		},
		methods:{
			login(){
				if (document.getElementById("text").value == ''){
					alert("请输入账号")
					return
				}
				if (document.getElementById("password").value == ''){
					alert("请输入密码")
					return
				}
				if (localStorage.getItem("name") === this.name && localStorage.getItem("password") === this.password) {
					this.name= ''
					this.password = ''
					this.$router.push({name:'Info'})
				}else{
					alert("用户名或密码错误")
					this.name = ''
					this.password = ''
				}
				
			},
			reg() {
				this.isReg = true
			},
			cancel() {
				this.isReg = false
			},
			adduser(){
				if (document.getElementById("reg_text").value == ''){
					alert("请输入账号")
					return
				}
				if (document.getElementById("reg_password").value == ''){
					alert("请输入密码")
					return
				}
				localStorage.setItem("name", this.name)
				localStorage.setItem("password", this.password)
				this.name = ''
				this.password = ''
				this.isReg = false
			},
		},
		mounted: function(){
			theInput()
		}
	}
	
</script>


<style lang="scss">
.login{
	width: 100vw;
	height: 100vh;
	background: url('../../assets/images/bg.png') no-repeat;
	background-size:100% auto;
	padding:60% 2% 0 2%;
	box-sizing: border-box; 
	position:relative;
}

section{
	.logo{
		font-size: 5rem;
		width: 10rem;
		color: white;
		position:absolute;
		top: 15%;
		left: 50%;
		margin-left: -5rem;
	}
	input{
		display: flex;
		margin: 0 auto;
		margin-bottom: 10%;
		font-size: 2.2rem;
		width: 60%;
		height: 50px;
		text-align: center;
		border:0;
		border-bottom:.5px solid rgba(0,0,0,.1);
	}
	button{
		width: 60%;
		height: 4rem;
		border:0;
		background:#0188fb;
		font-size: 2rem;
		color: white;
		
	}

	#logBtn{
		margin: 0 auto;
		display: block;
	}

	.login_left{
		position: absolute;
		bottom: 0;
		width: auto;
		text-align: center;
		height: 30px;
		line-height: 30px;
		font-size: 12px;
		color: #0188fb;
	}
	.login_right{
		right: 2%
	}

}

.sectionTwo{
	div{
		width: 100%;
		margin-left: -2%;
		position:absolute;
		bottom:0;
		button{
			width: 50%
		}
	}

}
</style>