function theInput(){
	var theInputA = document.querySelectorAll("input");
	for (var i = 0; i < theInputA.length; i++) {
		(function(arg){
			theInputA[i].onfocus = function(){
				theInputA[arg].style.borderColor = "#0188fb"
			}
			theInputA[i].onblur = function(){
				theInputA[arg].style.borderColor = ""
			}
		})(i)
	}
}



export{
	theInput
}